<template>
  <div id="subjects">
    <div id="summary" class="card">
      <div class="flex-center">
        Završna ocjena:
        <div
          id="final-grade-original"
          v-tooltip.top-start="{
            content:
              'Prosjek svih zaključnih ocjena (završni uspjeh).<br>' +
              'Ako ocjena iz nekog predmeta nije zaključena, zaokružuje se aritmetička sredina.' +
              '<br>Ako predmet nema ocjena, ne uračunava se u prosjek.',
            html: true,
          }"
        >
          {{ formatNum(finalGradeOriginal) }}
        </div>
        <transition name="opacity">
          <div
            v-if="finalGradeOriginal != finalGrade && !isNaN(finalGrade)"
            id="final-grade"
            v-tooltip.top-start="{
              content: 'Prosjek zaokruženih aritmetičkih sredina.',
              html: true,
            }"
            :style="{
              color: finalGradeOriginal > finalGrade ? 'green' : 'red',
            }"
          >
            <transition :name="finalGradeTransition" mode="out-in">
              <div v-bind:key="finalGradeTransition">
                {{ formatNum(finalGrade) }}
              </div>
            </transition>
          </div>
        </transition>
      </div>
      <div id="options" class="flex-center">
        <span
          v-for="(option, i) in options"
          :key="i"
          class="material-icons flex-center"
          :class="{ 'option-enabled': option.enabled }"
          :style="{ 'font-size': option.fontSize || '24px' }"
          v-tooltip="option.tooltip"
          v-wave
          @click="optionClicked(option)"
          >{{ option.icon }}</span
        >
      </div>
    </div>
    <SlickList
      id="subjects-list"
      :class="{ sorting: subjectsSorting }"
      v-model:list="subjects"
      axis="xy"
      helperClass="dragged-subject"
      :lockOffset="15"
      :lockToContainerEdges="true"
      :distance="5"
      @sort-start="subjectsSortingEvent(true)"
      @sort-end="subjectsSortingEvent(false)"
      @sort-cancel="subjectsSortingEvent(false)"
      @update:list="subjectsOrderChanged"
    >
      <SlickItem
        v-for="(subject, i) in subjects"
        :key="i"
        :index="i"
        class="subject"
        :class="{ 'expanded-keep': subject.expandedKeep }"
        @mouseenter="subjectMouseEnter($event, subject)"
        @mouseleave="subjectMouseLeave(subject)"
      >
        <div
          class="subject-head card"
          :ref="subject.url + 'head'"
          :style="{ 'margin-bottom': subject.marginBottom }"
        >
          <div class="subject-info" @wheel="onSubjectInfoWheel">
            <div class="subject-name">{{ subject.name }}</div>
            <div class="teachers">{{ subject.teachers }}</div>
          </div>
          <div class="grade-count">
            {{ getSubjectGradesCount(subject, subject.gradesByCategory) }}
          </div>
          <div
            class="grade-avg flex-center"
            contenteditable="true"
            @input="avgInputted($event, subject)"
            @focus="avgFocusChanged"
            @blur="avgFocusChanged"
          >
            {{
              formatNum(getSubjectGradesAvg(subject, subject.gradesByCategory))
            }}
          </div>
          <transition name="subject-colors">
            <div v-if="savedOptions.subjectColors" class="subject-colors"></div>
          </transition>
        </div>
        <transition name="subject-body">
          <div
            v-show="subject.expanded || subject.expandedKeep"
            class="subject-body card"
            :ref="subject.url + 'body'"
          >
            <table
              v-if="subject.gradesByCategory && subject.gradesByCategory.length"
            >
              <thead>
                <tr>
                  <td>
                    <div
                      class="pin-subject"
                      :class="{ pinned: subject.expandedKeep }"
                      @click="expandSubject(subject, !subject.expandedKeep)"
                    >
                      <span class="material-icons"> keyboard_capslock </span>
                      Zadrži
                    </div>
                  </td>
                  <td>IX</td>
                  <td>X</td>
                  <td>XI</td>
                  <td>XII</td>
                  <td>I</td>
                  <td>II</td>
                  <td>III</td>
                  <td>IV</td>
                  <td>V</td>
                  <td>VI</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(row, i) in subject.gradesByCategory" :key="i">
                  <td v-tooltip="row.name" :key="tooltipKey">
                    {{ row.name }}
                  </td>
                  <td
                    v-for="(grades, j) in row.grades"
                    :key="j"
                    contenteditable="true"
                    @input="gradeInputted($event, grades)"
                  >
                    {{ grades.join(", ") }}
                  </td>
                </tr>
              </tbody>
            </table>
            <div v-else class="no-grades">Predmet nema ocjena.</div>
          </div>
        </transition>
      </SlickItem>
    </SlickList>

    <!-- SUBJECT -->
    <router-view v-slot="{ Component }">
      <transition name="opacity">
        <component :is="Component" />
      </transition>
    </router-view>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { SlickList, SlickItem } from "vue-slicksort";
import { updateSubjects } from "@/scripts/scrapers";
import { User, ClassInfo, SubjectCache, GradesByCategory } from "@/store/state";
import { formatNum, parseNum, setEndOfContenteditable } from "@/scripts/utils";

/* import Spinner from "@/components/Spinner.vue";
 */

interface ExtendedSubjectCache extends SubjectCache {
  expanded: boolean;
  expandedKeep: boolean;
  marginBottom?: string;
  gradesByCategoryOriginal: GradesByCategory[];
  gradesCount: number;
  gradesAvg: number;
}

interface Option {
  icon: string;
  tooltip: string;
  name: string;
  fontSize?: string;
  enabled?: boolean;
}

export default defineComponent({
  name: "Subjects",
  components: { SlickList, SlickItem /* Spinner */ },
  props: { classURL: String },
  /* emits: ["sectionLoading", "sectionLoaded"], */ // TODO: FIX TS ERROR ???
  created() {
    this.updateSubjects();
  },
  data() {
    return {
      tooltipKey: 1,
      savedOptions: {
        subjectColors: false,
      },
      options: [
        {
          icon: "unfold_more",
          tooltip: "Otvori sve tablice",
          enabled: false,
          name: "expandTables",
        },
        {
          icon: "zoom_in",
          tooltip: "Povećaj prikaz",
          name: "zoomIn",
        },
        {
          icon: "zoom_out",
          tooltip: "Smanji prikaz",
          name: "zoomOut",
        },
        {
          icon: "border_color",
          tooltip: "Prikaži boje ocjena",
          fontSize: "19px",
          name: "subjectColors",
          enabled: false,
        },
        {
          icon: "functions",
          tooltip: "Zbroji zaključne ocjene",
          name: "countAvgs",
        },
        {
          icon: "import_export",
          tooltip: "Sortiraj predmete",
          name: "sortSubjects",
        },
        {
          icon: "sync",
          tooltip: "Ažuriraj sve ocjene",
          name: "updateSubjects",
        },
      ] as Option[],
      subjects: [] as ExtendedSubjectCache[],
      subjectsLoading: false,
      subjectsSorting: false,
      finalGradeOriginal: 0,
      finalGradeTransition: "number-up",
    };
  },
  methods: {
    async updateSubjects(forceUpdate?: true) {
      if (!this.openedClassInfo || this.subjectsLoading) return;
      this.$emit("sectionLoading");
      this.subjectsLoading = true;
      if (!(await updateSubjects(this.openedClassInfo, forceUpdate))) return;
      this.subjectsLoading = false;
      this.$emit("sectionLoaded");
      this.resetSubjects();
    },
    resetSubjects() {
      const cached =
        this.openedClassInfo && this.openedClassInfo.cachedSubjects;
      if (!cached || !cached.length) return;
      this.subjects = cached.map((subject) => ({
        ...subject,
        gradesByCategoryOriginal: JSON.parse(
          JSON.stringify(subject.gradesByCategory || []),
        ),
      })) as ExtendedSubjectCache[];
      this.finalGradeOriginal = this.getFinalGradeOriginal();
    },
    subjectMouseEnter(e: MouseEvent, subject: ExtendedSubjectCache) {
      const target = e.target as HTMLElement;
      setTimeout(() => (subject.expanded = target.matches(":hover")), 150);
    },
    subjectMouseLeave(subject: ExtendedSubjectCache) {
      subject.expanded = false;
    },
    onSubjectInfoWheel(e: WheelEvent) {
      const path = ((e as any).path ||
        (e.composedPath && e.composedPath()) ||
        []) as HTMLElement[];
      const target = path.find((el) => el.className == "subject-info");
      if (!target) return;
      const toLeft = e.deltaY < 0 && target.scrollLeft > 0;
      const toRight =
        e.deltaY > 0 &&
        target.scrollLeft < target.scrollWidth - target.clientWidth;
      if (toLeft || toRight) {
        e.preventDefault();
        target.scrollBy({ left: e.deltaY, behavior: "smooth" });
      }
    },
    subjectsSortingEvent(started: boolean) {
      this.subjectsSorting = started;
    },
    subjectsOrderChanged() {
      this.tooltipKey += 1; // todo: fix tooltip reactivity bug
    },
    expandSubject(subject: ExtendedSubjectCache, expand: boolean) {
      subject.expandedKeep = expand;
      this.$nextTick(() => {
        const subjectBody = this.$refs[subject.url + "body"] as HTMLElement;
        const margin = expand ? subjectBody.offsetHeight + 10 + "px" : "0px";
        subject.marginBottom = margin;
      });
    },
    optionClicked(option: Option) {
      if (option.enabled !== undefined) option.enabled = !option.enabled;
      const enabled = option.enabled || false;
      switch (option.name) {
        case "subjectColors":
          this.savedOptions.subjectColors = enabled;
          break;
        case "expandTables":
          this.subjects.forEach((s) => this.expandSubject(s, enabled));
          break;
      }
    },
    gradeInputted(e: InputEvent, grades: number[]) {
      const target = e.target as HTMLElement;
      const _grades = (target.textContent as string).match(/[1-5]/g);
      target.textContent = _grades ? _grades.join(", ") : "";
      grades.length = 0;
      grades.push(...(_grades ? _grades.map(Number) : []));
      this.$nextTick(() => target && setEndOfContenteditable(target));
    },
    avgInputted(e: InputEvent, subject: ExtendedSubjectCache) {
      const target = e.target as HTMLElement;
      if (!target.textContent) {
        subject.gradesAvg = NaN;
      } else if (e.data && /[1-5]/.test(e.data)) {
        subject.gradesAvg = parseInt(e.data);
        target.textContent = e.data;
      } else {
        target.textContent = "";
      }
      this.$nextTick(() => target && setEndOfContenteditable(target));
    },
    avgFocusChanged(e: FocusEvent) {
      const target = e.target as HTMLElement;
      if (e.type == "focus" && target.textContent == "—") {
        target.textContent = "";
      } else if (e.type == "blur" && !target.textContent) {
        target.textContent = "—";
      }
    },
    getSubjectGradesCount(
      subject: ExtendedSubjectCache,
      gradesByCategory?: GradesByCategory[],
      original?: boolean,
    ): number {
      const gradesCount = (gradesByCategory || []).reduce((a, row) => {
        return a + row.grades.flat().length;
      }, 0);
      if (!original) subject.gradesCount = gradesCount;
      return gradesCount;
    },
    getSubjectGradesAvg(
      subject: ExtendedSubjectCache,
      gradesByCategory?: GradesByCategory[],
      original?: boolean,
    ): number {
      const gradesAvg =
        (gradesByCategory || []).reduce((a, row) => {
          return a + row.grades.flat().reduce((b, grade) => b + grade, 0);
        }, 0) / this.getSubjectGradesCount(subject, gradesByCategory, original);
      if (!original) subject.gradesAvg = gradesAvg;
      return gradesAvg;
    },
    getFinalGradeOriginal(): number {
      const subjectsWithGrades = this.subjects.filter((subject) => {
        const params = [subject, subject.gradesByCategoryOriginal, true];
        return (this.getSubjectGradesCount as any)(...params);
      });
      return (
        subjectsWithGrades.reduce((a, subject) => {
          if (subject.finalGrade) return a + subject.finalGrade;
          const params = [subject, subject.gradesByCategoryOriginal, true];
          return a + Math.round((this.getSubjectGradesAvg as any)(...params));
        }, 0) / subjectsWithGrades.length
      );
    },
    formatNum: (num: number): string => formatNum(num),
  },
  computed: {
    user(): User | undefined {
      return this.$store.getters.user;
    },
    openedClassInfo(): ClassInfo | undefined {
      return this.$store.getters.classInfo(this.classURL || "");
    },
    allSubjectsExpanded(): boolean {
      return this.subjects.every((subject) => subject.expandedKeep);
    },
    finalGrade(): number {
      const subjectsWithGrades = this.subjects.filter((subject) => {
        return !isNaN(subject.gradesAvg);
      });
      return (
        subjectsWithGrades.reduce((a, subject) => {
          return a + Math.round(subject.gradesAvg);
        }, 0) / subjectsWithGrades.length
      );
    },
  },
  watch: {
    classURL(to, from) {
      from && this.updateSubjects();
    },
    allSubjectsExpanded(yes) {
      this.options.find((o) => o.tooltip == "Otvori sve tablice")!.enabled =
        yes;
    },
    finalGrade(_new, prev) {
      this.finalGradeTransition = _new > prev ? "number-up" : "number-down";
      console.log(this.finalGradeTransition);
    },
  },
});
</script>

<style lang="scss" scoped>
$subject-peek-duration: 150ms;
$subject-peek-delay: 150ms;

#subjects {
  display: flex;
  flex-wrap: wrap;
}

#summary {
  flex: 1 1 100%;
  display: flex;
  font-size: 24px;
  padding: 0 30px;
  margin: 0 10px 40px;
  color: $main-color;
  background: linear-gradient(
      45deg,
      transparent 55%,
      #e3eaf1 65%,
      aliceblue calc(65% + 10px)
    ),
    linear-gradient(-45deg, white 65%, #e3eaf1 calc(75% - 10px), aliceblue 75%);
}

#final-grade-original {
  padding-left: 10px;
}

#final-grade {
  border-left: 1px solid $light-border-color;
  padding-left: 10px;
  margin-left: 10px;
  transition: color 500ms;
}

#options {
  color: $user-color;
  margin-left: auto;

  & > span {
    width: 24px;
    height: 24px;
    padding: 10px 10px;
    transition: background-color 150ms;

    &:hover {
      cursor: pointer;
      background: #dfebf5;
    }
  }
}

.option-enabled {
  color: $navbar-selected-text-color;
}

/* subjects */

#subjects-list {
  flex: 1 1 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));

  &.sorting > .subject {
    pointer-events: none;
    user-select: none;
  }
}

.subject {
  position: relative;
  margin: 5px 10px;
  white-space: nowrap;

  &.expanded-keep > div {
    background: white;
    transition-delay: unset !important;
  }

  &:hover {
    & > div {
      background: aliceblue;
    }

    & > .subject-head {
      z-index: 4;
      transition: background-color $subject-peek-duration $subject-peek-delay,
        margin-bottom 500ms;
    }

    & > .subject-body {
      z-index: 3;
      transition: background-color $subject-peek-duration $subject-peek-delay,
        opacity $subject-peek-duration, transform $subject-peek-duration;
    }
  }
}

.dragged-subject > .subject-head {
  background: white;
  transition: none;
}

/* subject head */

.subject-head {
  position: relative;
  height: 44px;
  display: flex;
  align-items: center;
  overflow: hidden;
  border-radius: 8px;
  background: transparent;
  margin-bottom: 0;
  transition: background-color $subject-peek-duration $subject-peek-delay,
    margin-bottom 500ms, z-index 0ms $subject-peek-duration;
  z-index: 2;
  transform: translateZ(0);
  will-change: margin, margin-bottom;
}

.subject-info {
  display: flex;
  overflow-x: auto;
  margin: 0 auto 0 30px;

  &::-webkit-scrollbar {
    display: none;
  }
}

.subject-name {
  color: $hovered-text-button;
  font-weight: bold;
}

.teachers,
.grade-count {
  color: $light-gray-text;
  border-left: 1px solid $light-border-color;
  margin-left: 15px;
}

.teachers {
  padding-right: 30px;
  padding-left: 15px;
}

.grade-count {
  position: relative;
  flex-shrink: 0;
  width: 50px;
  text-align: center;

  &::after {
    content: "";
    position: absolute;
    left: -45px;
    height: 100%;
    width: 30px;
    background: linear-gradient(to right, transparent, white);
  }
}

.grade-avg {
  flex-shrink: 0;
  height: 100%;
  line-height: 44px;
  width: 75px;
  border-left: 1px solid $light-border-color;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0 0 5px #cacccc;
  clip-path: inset(1px 1px 1px -5px);
  outline: none;
}

.subject-colors {
  position: absolute;
  width: 100%;
  bottom: 0;
  height: 4px;
  background: red;
  transition: opacity 500ms, transform 500ms;
  transform-origin: left;
}

/* subject body */

.subject-body {
  position: absolute;
  left: -4px;
  right: -4px;
  padding-top: 10px;
  top: 40px;
  font-size: 14px;
  background: white;
  transition: background-color $subject-peek-duration $subject-peek-delay,
    opacity $subject-peek-duration, transform $subject-peek-duration,
    z-index 0ms $subject-peek-duration;
  transform-origin: top;
  overflow: hidden;
  z-index: 1;
}

.pin-subject {
  cursor: pointer;
  display: flex;
  align-items: center;
  user-select: none;
  transition: color 150ms;

  &.pinned {
    color: $button-color;
  }

  span {
    padding: 0 10px 0 24px;
  }
}

table {
  table-layout: fixed;
  white-space: pre-line;
  width: 100%;
  border-spacing: 0;
}

thead {
  font-weight: bold;
}

td {
  border: 1px solid #eeeeee;
  border-radius: 4px;
  text-align: center;
  padding: 3px 5px;
  overflow: hidden;
  outline: none;

  &:first-child {
    width: 150px;
    text-align: left;
    white-space: pre;
    text-overflow: ellipsis;
  }
}

.no-grades {
  text-align: center;
  padding: 10px;
}

/* transitions */

.number-up-active,
.number-down-active,
.number-up-active,
.number-down-active {
  transition: 1s !important;
}

.number-up-enter-from,
.number-down-leave-to {
  opacity: 0;
  transform: translateY(-10px);
}

.number-down-enter-from,
.number-up-leave-to {
  opacity: 0;
  transform: translateY(10px);
}

.subject-colors-enter-from,
.subject-colors-leave-to {
  opacity: 0;
  transform: scaleX(0);
}

.subject-body-enter-from {
  opacity: 0;
  transform: scaleY(0.5) translateY(-10px);
}
.subject-body-leave-to {
  opacity: 0;
  transform: scale(0.97);
}
</style>
